
import avatarSrc from '../userHeader/avator.png'
import Scroll from '../../../base/scroll/scroll'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import { mapActions } from 'vuex'
import { userInfoMixin } from '../../../assets/js/mixin'
export default {
name: "settings",
mixins:[userInfoMixin],
filters: {
isNull: function(value) {
if (value) {
return value
}
return ''
}
},
components: {
Group,
Cell,
Scroll,
Actionsheet,
Datetime
},
created () {
this.avatorBox.avator = this.userInfo.headPicId || avatarSrc
this.userName.value = this.userInfo.username
this.userTaobao.value = this.userInfo.taobaoLevel
this.userSex.value = (this.userInfo.gender===0?'':'')
this.userAge.value = this.userInfo.birthday
this.userAddress.value = this.userInfo.postAddress
this.boxTwo[0].value = this.userInfo.qqNum
this.boxTwo[1].value = this.userInfo.telephone
},
data() {
return {
avatorBox: {
title: '',
link: '',
isLink: true
},
userName: {
title: '',
link: '',
isLink: false,
},
userTaobao: {
title: '',
link: '',
isLink: true,
value: '',
info: ''
},
userSex: {
title: '',
link: 'settings/usersex',
isLink: true,
value: ''
},
userAge: {
title: '',
link: '',
isLink: true,
value: ''
},
userAddress: {
title: '',
link: 'settings/userAddress',
isLink: true,
value: ''
},
boxTwo: [
{
title: 'QQ',
link: 'settings/userQq',
isLink: true,
value: '279332222',
info: ''
},
{
title: '',
link: 'settings/userPhone',
isLink: true,
value: '17745812564'
}
],
show: false,
menus2: {
menu1: '',
menu2: ''
},
visibility: false,
age: ''
}
},
watch: {
age(val) {

}
},
methods: {

isNull(value) {
if (value) {
return value
}
return ''
},

avatorChange() {
this.show = true
},

ageChange() {
this.visibility = true
},
saveAge() {
this.$axios.post('/api/user/update', {
telephone: this.$store.state.userInfo.telephone,
birthday: this.age
}).then((response) => {
console.log(response);
let that = this;
let obj = this.$store.state.userInfo;
obj.birthday = this.age;
this.setUserInfo(obj);
this.$vux.toast.show({
text: '',
onHide () {
that.$router.push({ name: 'settings' })
}
})
}).catch((error) => {
console.log(error)
})
},

logout() {
const _this = this
this.$vux.confirm.show({
title: '',
content: '',
onConfirm() {
_this.$axios.post('/api/user/loginOut', {}).then((response) => {
console.log(response)
_this.setUserInfo('')
_this.$router.push({ name: 'login' })
}).catch((error) => {
console.log(error)
})
}
})
},

updateUserInfo(updateObj) {
this.$axios.post('/api/user/update',updateObj).then((response) => {
}).catch((error) => {
console.log(error)
})
},
...mapActions([
'setUserInfo'
])
}
}



